:root {
    --white: #ffffff;
    --bgBlue: #020014;
    --darkBlue: #050023;
    --mediumBlue: rgb(0, 16, 194);
    --greyBlue: rgb(22, 26, 51);
    --greyMediumBlue: rgb(24, 33, 97);
    --lightGrey: #bebebe;
    --black: #000000;
    --brightBlue: #a6a8ff;
}

* {
    box-sizing: border-box;
    margin: 0;
    text-decoration: none;
}

a {
    all: unset;
    user-select: none;

    cursor: pointer;
}

.input,
textarea {
    all: unset;
    border: 1px solid var(--brightBlue);
    margin: 20px 20px 20px 20px;
    border-radius: 3px;
    padding: 10px 10px 10px 10px;
}

.html {
    width: 100%;
    margin: 0;
    padding: 0;
    font-family: 'Roboto', sans-serif;
    font-size: 16px;
    color: var(--brightBlue);

}

body {
    margin: 0;
    display: flex;
    flex-direction: column;
    min-height: 100%;
    width: 100%;
    width: -webkit-fill-available;
    align-content: left;
    background-color: var(--bgBlue);
}

.header {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    position: fixed;
    position: -webkit-sticky;
    width: 100%;
    height: 80px;
    top: 0;
    flex-direction: row;
    color: var(--brightBlue);
    z-index: +5;
    padding: 10px 0 10px 0;
    background-color: var(--bgBlue);

}

.header-bar-left {
    text-decoration: none;
    display: flex;
    flex-direction: row;
    justify-content: left;
    align-items: center;
    font-size: 1.2rem;
    width: 60%;
    padding-left: 0;
    height: 100%;
    color: var(--white);
    transition: 200ms ease-in-out;

}

.header-bar-left a {
    margin: 0 2vw 0 2vw;
    padding: 10px 5px 10px 5px;
    border-radius: 10%;
}

.header-bar-left a:hover {
    margin: 0 2vw 0 2vw;
    color: var(--brightBlue);
    border-radius: 10%;
    transition: 100ms ease-in-out;
}

.header-bar-right {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 30%;
    font-size: 0.8rem;
    padding-right: 5px;
    height: 100%;
    color: var(--white);
}

.btn.active {
    text-decoration: none;
    background: rgb(1, 1, 70);
    color: var(--brightBlue);
    padding: 10px 5px 10px 5px;
}

.aboutme {
    display: flex;
    flex-direction: row;
    position: relative;
    width: 100%;
    height: 400px;
    color: var(--white);
    margin-top: 80px;
    padding: 0 5vw 0 0;
}

.aboutme-left-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 60%;
    height: 100%;
    color: var(--white);
    padding: 0 0 0 0;
}

.aboutme-right-container {
    display: flex;
    width: 40%;
    height: 100%;
    color: var(--white);
    padding: 0 0 0 0;
}


.aboutme-right-container img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
    border-radius: 1vw;
}

.page {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
    width: 100%;
    color: var(--white);
    padding: 0 0 0 0;
    margin-top: 20px;
}

.webProjects-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
    width: 100%;
    color: var(--white);
    padding: 0 0 0 0;
    margin-top: 80px;

}

.webProjects-header {
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-indent: 0vw;
    align-items: left;
    position: relative;
    width: 100%;
    height: 80px;
    color: var(--white);
    padding: 0 0 0 0;

}

.webProjects-header h3 {
    font-size: 1.5rem;
    border-bottom: var(--brightBlue) 1px solid;
    padding: 5px 0 5px 0;
    margin: 0 5vw 0 5vw;

}

.webProjects-cards {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 20px;
    width: 100%;
    color: var(--white);
    padding: 10px;
    margin: 0 0 0 0;

}

.card {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    height: auto;
    color: var(--white);
    padding: 10px;
    margin: 10px;

}

.card img {
    display: inline-block;
    width: 22vw;
    border-radius: 1vw;
    margin: 0;
}

.card p {
    padding: 20px 4vw 0 4vw;
    text-align: justify;
    text-indent: 50px;
    line-height: 1.5;
}

.GitHub-link {
    padding: 20px 10px 20px 10px;
    margin: 10px 0 10px 0;
    color: var(--white);
    font-size: 0.8rem;
}

.aboutme-page {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
    width: 100%;
    height: auto;
    color: var(--white);
    padding: 0 5vw 300px 5vw;
    margin-top: 100px;

}

.aboutme-page p {
    font-size: 1.2rem;
    padding: 5px 0 5px 0;
    margin: 0 5vw 0 5vw;
    text-align-last: left;
    text-align: justify;
    text-indent: 5vw;

}

.aboutme-page h1 {
    font-size: 2rem;
    margin: 0 0 20px 0;
    padding: 0 0 20px 0;
    text-align: center;
    width: 89%;
    border-bottom: var(--brightBlue) 1px solid;
}

.contact {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
    width: 100%;
    height: 60vh;
    color: var(--white);
    margin-top: 20px;
    padding: 0 0 0 0;

}

.contact-form {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
    width: 100%;
    color: var(--white);
    margin-top: 20px;
    padding: 0 0 0 0;

}

.contact h1 {
    margin-top: 80px;
    font-size: 2rem;
    margin: 0 0 20px 0;
    padding: 0 0 20px 0;
    text-align: center;
    width: 89%;
    border-bottom: var(--brightBlue) 1px solid;
}

.input,
.contact-form textarea {
    width: 50%;
    margin: 10px, 10px, 10px, 10px;
    color: var(--black);
    background-color: var(--lightGrey);
}

.submit {
    text-decoration: none;
    width: 100px;
    height: 40px;
    background: var(--brightBlue);
    color: var(--black);
    font-weight: bolder;
    font-size: 1rem;
    padding: 10px 5px 10px 5px;
    border-radius: 5px;
    transition: 200ms ease-in-out;
    cursor: pointer;
}

.error-message {
    color: var(--brightBlue);
    font-size: 1rem;
    margin: 0 0 0 0;
    padding: 5px 0 0 0;
}

.resume {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
    width: 100%;
    height: auto;
    color: var(--white);
    margin-top: 80px;
    padding: 0 250px 40px 250px;
    text-align: left;
}

.resume h1 {
    font-size: 2rem;
    margin: 0 0 20px 0;
}

.resume-header {
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-indent: 0vw;
    align-items: left;
    position: relative;
    width: 100%;
    height: 80px;
    color: var(--white);
    padding: 0 0 0 0;
    margin-bottom: 20px;
    margin-top: 30px;
}

.resume-header h2 {
    font-size: 1.5rem;
    border-bottom: var(--brightBlue) 1px solid;
    padding: 5px 0 5px 0;
    margin: 0 5vw 0 5vw;
}

.resume p {
    font-size: 1.2rem;
    padding: 5px 0 5px 0;
    margin: 0 5vw 0 5vw;
    text-align-last: left;
    text-align: justify;
    text-indent: 5vw;
}

.resume span {
    color: var(--brightBlue);
    font-weight: 700;
    margin: 10px 0 10px 0;
}

.resume-end {
    border-bottom: var(--brightBlue) 1px solid;

}

.button-resume {
    text-decoration: none;
    width: 200px;
    height: 40px;
    background: var(--brightBlue);
    color: var(--black);
    font-weight: bolder;
    font-size: 1rem;
    margin-bottom: 80px;
    padding: 10px 5px 10px 5px;
    border-radius: 5px;
    transition: 200ms ease-in-out;
    cursor: pointer;
}

.button-resume:hover {
    background: var(--white);
    color: var(--brightBlue);
    transition: 200ms ease-in-out;
}



















.input:focus {
    border: 5px solid var(--white);
    transition: 300ms ease-in-out;
    background-color: var(--white);
}

.hover:hover {

    color: var(--brightBlue);
    border-radius: 10%;
    transition: 300ms ease-in-out;
}

.centered {
    justify-content: center;
    padding: 0 0 0 0;
}

footer {
    position: relative;
    width: 100%;
    height: auto;
    bottom: 0;
    padding: 20px;
    text-align: center;
    background: var(--bgBlue);
    color: var(--white);
    margin-top: auto;

}

footer h3 {
    font-size: 150%;
    line-height: 45px;
}

footer a img {
    height: 45px;
    margin-left: 15px;
    margin-right: 15px;
}

.centered {
    justify-content: center;
    padding: 0 0 0 0;
}

.hidden {
    display: none;
}

@media screen and (max-width: 1400px) {
    .resume {
        padding: 0 150px 40px 150px;
    }
}


@media screen and (max-width: 1000px) {

    .header-bar-left,
    .header-bar-right {
        font-size: 90%;

    }

    .header-bar-right {
        font-size: 80%;
    }

    .webProjects-cards {
        grid-template-columns: 1fr 1fr;
    }

    .card img {
        width: 40vw;
    }

    .double-images {
        flex-direction: column;
        height: 800px;
        padding: 0 0 0 0;

    }

    .double-images a {
        margin: 0 0 20px 0;
        max-width: 80%;

    }

    .double-images img {
        max-width: 100%;
    }

    .resume {
        padding: 0 20px 40px 20px;
    }

}

@media screen and (max-width: 890px) {

    .hover:hover,
    .header-bar-left a:hover {
        color: var(--white);

    }

    .header-bar-left,
    .header-bar-right {
        font-size: 90%;
        width: 100%;
        text-align: center;
        justify-content: center;
    }

    .header-bar-right {
        width: 100%;
    }

    header {
        flex-direction: column;
        height: 120px;
    }

    .aboutme {
        margin-top: 120px;
    }

    .aboutme-page {
        margin-top: 150px;
    }

    .webProjects-cards {
        grid-template-columns: 1fr 1fr;
    }

    .card img {
        width: 40vw;
    }

    .double-images {
        flex-direction: column;
        height: auto;
        padding: 40px 0 20px 0;

    }

    .double-images a {
        margin: 0 0 20px 0;
        max-width: 80%;

    }

    .double-images img {
        max-width: 100%;
    }

}


@media screen and (max-width: 660px) {

    .webProjects-cards {
        grid-template-columns: 1fr;
    }

    .card img {
        width: 80vw;
    }

    .contact div {
        width: 80%;
    }

    .aboutme {
        height: 270px;
        padding: 0 5vw 0 5vw;
    }

    .aboutme-left-container {

        width: 40vw;

    }

    .aboutme img {
        width: 50vw;

    }

    .contact div {
        font-size: 65%;
        height: 500px;
    }

    .aboutme {
        flex-direction: column;
        justify-content: center;
        align-items: center;

    }

    .aboutme-right-container {
        width: 100%;
        justify-content: center;
        align-items: center;
        margin: 0 0 0 0;
    }

    .aboutme-left-container {
        width: 100%;
        justify-content: center;
        align-items: center;
        margin: 0 0 0 0;
    }

    .resume p {
        font-size: .8rem;
    }


}


@media screen and (max-width: 415px) {

    
    .btn {
        font-size: 85%;
        padding: 10px 20px 10px 20px;
        margin: 0 0 0 0;
    }


}